function initMaps() {
  var adress = {lat: 54.409778, lng: 18.596477};
  var map = new google.maps.Map(document.getElementById('map1'), {
    zoom: 16,
    center: adress
  });
  var marker = new google.maps.Marker({
    position: adress,
    map: map
  });

  var adress2 = {lat: 54.377892, lng: 18.633727};
  var map2 = new google.maps.Map(document.getElementById('map2'), {
    zoom: 16,
    center: adress2
  });
  var marker2 = new google.maps.Marker({
    position: adress2,
    map: map2
  });
}